.cv-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    @include scroll-snap-align-start;
    float: right;

    background-color: $color2;

    &__cv-title {
        font-size: 5rem;
        margin-top: 100px;
        color: $color1;
    }

    &__container {
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;

        &__download-box {
        width: 40%;
        height: 400px;
        background-color: rgba(252, 81, 48, 0.8);
        border-radius: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        &__download-link {
            transition: all .5s;
            text-decoration: none;
            color: $color1;

            &:hover {
                color: $color2;
                transform: translateY(-4px);
                
            }

            &__text {
                padding-bottom: 40px;
                font-size: 1.8rem;
                color: $color1;
                text-align: center;
                font-weight: bold;
                white-space: pre-wrap;
            }
        }
        }
    }
}
