.portfolio-page {
    min-height: 100vh;
    width: 85%;
    @include center-div;
    float: right;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    
    background-color: $color2;

    &__portfolio-title {
        font-size: 5rem;
        margin-top: 50px;
        margin-bottom: 10px;
        color: $color1;
    }
}

.portfolio-gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    column-gap: 20px;
    row-gap: 20px;
    padding: 30px;

    &__box {
    position: relative;
    border: 5px solid $color1;
    border-radius: 25px;
    background-color: rgba(240, 247, 244, 0.8);
    

        &__info {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            flex-direction: column;
            height: auto;
            border-bottom-left-radius: 25px;
            border-bottom-right-radius: 25px;
            width: 100%;
            padding: 30px;
            border-top: 5px solid $color1;
            
            h1 {
                font-size: 2.8rem;
                color: $color1;
                margin-top: 10px;
                margin-bottom: 10px;
            }

            p{
                font-size: 1.8rem;
                color: $color1;
                margin-top: 10px;
                margin-bottom: 10px;
            }

            &__language-icons {
                display: flex;
                width: 200px;
                align-items: center;
                justify-content: space-evenly;
            }

        }

    }

    img {
        width: 100%;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
    }
}
