.about-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    @include scroll-snap-align-start;
    float: right;
    background-color: $color2;

    &__about-title {
        font-size: 5rem;
        margin-top: 100px;
        color: $color1;
    }

    &__images{
        padding-top: 50px;
        &__portrait {
            border-radius: 50%;
            height: 180px;
            width: 180px;
            object-fit: cover;
            scale: 0.5;
            border: 5px solid $color4;
            
        }
    }

    &__bio-wrapper {
        width: 70%;
        padding-top: 50px;
        &__bio{
        font-size: 2rem;
        }
    }

    &__social-icons {
        display: flex;
        justify-content: space-around;
        width: 150px;
        margin-top: 100px;
        border: 2px solid $color4;
        padding: 10px;
        border-radius: 10px;

        &__icon {
            color: $color1;
            transition: all .5s;

            &:hover {
                color: $color4;
                transform: translateY(-2px);
            }
        }

    }
}